.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background: #3d352c;
  color: white;
  min-width: 160px;
  z-index: 1;
  top: 46px;
}
.dropdown-content span {
  display: inline-block;
  margin-left: 8px;
  font-size: 12px;
}
.dropdown-content:hover {
  display: block;
}

.dropdown-content a {
  text-decoration: none;
  display: block;
  color: #fff;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 17px;
  padding:10px 16px;
}
.dropdown-content a.padding{
  padding: 10px 20px 10px 16px;
}

.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown-content a:hover {
  color: #e90101;
}
.dropdownresearch {
  position: relative;
  display: inline-block;
  width: 100%;
}
.dropdownresearch:hover .dropdown-contentresearch {
  display: block;
}
.dropdown-contentresearch{
  display: none;
  position: absolute;
  background: #3d352c;
  color: white;
  min-width: 160px;
  z-index: 1;
  left: 159px;
  top:-17px;
}
.dropdown-contentresearch a{
  padding-right: 0;
}

